@import "functions";
@import "mixins";
@import "fonts";
@import "variables";
@import "blocks";

* {
  margin: 0;
  padding: 0;
  outline: none;
}

html,
body {
  height: 100%
}

#root {
  height: 100%
}

a {
  color: #777879;
  text-decoration: none;

  &:visited {
    color: inherit;
  }

  &:hover {
    text-decoration: underline;
  }
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  font-size: 16px;
  color: #777879;
  background-color: #FBFBFB;
}

.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
  margin-bottom: 80px;
}

.theme {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: white;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.15), -1px -1px 2px rgba(0, 0, 0, 0.04), inset -1px -1px 3px rgba(0, 0, 0, 0.1);
  position: relative;
  transform: translateX(50%);
  transition: transform ease 0.28s;

  &:hover{
    transform: translateX(-50%);
  }
}

.theme__wrapper {
  width: 48px;
  height: 24px;
  opacity: 0.2;
  background-color: #777879;
  border-radius: 22px;
  display: flex;
  justify-content: center;
  box-shadow: inset 0px 0px 3px rgba(0, 0, 0, 1);
}
